.social-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  & > span,
  & > a {
    display: inline-flex;
  }
}

@media screen and (max-width: 460px) {
  .social-item {
    display: flex;
    flex-direction: row;
    align-items: center;
    & > span,
    & > a {
      display: inline-flex;
    }
  }
} ;
